/**
 * @author xiejiahe <xjh22222228@gmail.com>
 * @example:
 * <Table
 *   // ref
 *   ref={tableRef}
 *
 *   // getData axios Promise 
 *   getTableData={getData}
 *
 *   columns={tableColumns}
 * />
 */

import React, { FC, useEffect } from 'react';
import './style.scss';
import { Table } from 'antd';
import { TableProps } from 'rc-table/lib/Table';
import { AxiosPromise } from 'axios';
import useKeepState from 'use-keep-state';
import ActionPanel from './action-panel';

interface Props {
  getTableData: (data: any) => AxiosPromise;
  onTableChange?: (pagination: any, filters: any, sorter: any) => void;
  onDelete?: (id: string) => AxiosPromise;
  onAdd?: () => void;
  [key: string]: any;
}

interface State {
  tableHeight: number;
  tableDataSource: any[];
  isLoading: boolean;
  pagination: {
    [key: string]: any;
  },
  selectedRowKeys: string[]
}

const DEFAULT_PAGE_SIZE = 50;
const initialState: State = {
  tableHeight: 0,
  tableDataSource: [],
  isLoading: false,
  pagination: {
    pageNo: 1,
    pageSize: DEFAULT_PAGE_SIZE,
    showSizeChanger: true,
    total: 0,
    pageSizeOptions: ['30', '50', '70', '100', '200']
  },
  selectedRowKeys: []
};

const TableFC: FC<Props & TableProps<unknown>> = ({
  getTableData,
  onTableChange,
  onDelete,
  onAdd,
  forwardedRef: tableRef,
  ...props
}) => {
  let rowSelection;
  const showRowSelection = onDelete;
  const [state, setState] = useKeepState(initialState);

  function getData() {
    setState({ isLoading: true });
    const { pageNo, pageSize } = tableRef.current;
    // 
    getTableData({
      pageNo: pageNo - 1,
      pageSize: pageSize
    })
      .then(res => {
        if (res.data.success) {
          setState({
            pagination: {
              ...state.pagination,
              total: res.data.data.count,
              pageSize
            },
            tableDataSource: res.data.data.rows
          });
        }
      })
      .finally(() => {
        setState({ isLoading: false });
      });
  }

  function onChange(pagination: any, filters: any, sorter: any) {
    const pageNo = pagination.current;
    const pageSize = pagination.pageSize;
    setState({
      pagination: {
        ...state.pagination,
        pageNo,
        pageSize
      }
    });
    tableRef.current.pageNo = pageNo;
    tableRef.current.pageSize = pageSize;
    getData();
    onTableChange && onTableChange(pagination, filters, sorter);
  }

  useEffect(() => {
    if (!tableRef.current) {
      tableRef.current = {};
    }
    // 
    tableRef.current.getTableData = getData;
  });

  useEffect(() => {
    tableRef.current.pageNo = 1;
    tableRef.current.pageSize = DEFAULT_PAGE_SIZE;
  }, [tableRef]);

  useEffect(() => {
    // 
    setTimeout(() => {
      const tableEl = document.querySelector('.ant-table-wrapper');
      if (tableEl) {
        setState({ tableHeight: tableEl.clientHeight - 120 });
      }
    }, 0);
  }, [setState]);

  function handleDelete() {
    if (!onDelete) return null;
    const selectedRowKeys = state.selectedRowKeys.join(',');
    onDelete(selectedRowKeys)
      .then(res => {
        if (res.data.success) {
          setState({ selectedRowKeys: [] });
          getData();
        }
      });
  }

  if (showRowSelection) {
    rowSelection = {
      onChange(selectedRowKeys: string[]) {
        setState({ selectedRowKeys });
      }
    };
  }

  return (
    <React.Fragment>
      <ActionPanel
        selectedRowKeys={state.selectedRowKeys}
        onDelete={onDelete && handleDelete}
        onAdd={onAdd}
      />

      <Table
        {...props as any}
        rowKey="id"
        loading={state.isLoading}
        dataSource={state.tableDataSource}
        scroll={{ y: state.tableHeight + 'px' }}
        showHeader={state.tableDataSource.length}
        onChange={onChange}
        rowSelection={rowSelection}
        pagination={{
          ...state.pagination,
          size: 'small'
        }}
      />
    </React.Fragment>
  )
};

const forwardedTable = React.forwardRef((props: any, ref) => (
  <TableFC {...props} forwardedRef={ref} />
));

export default React.memo(forwardedTable);
